export async function enqueueJobs(jobs: any[]) {
  const base  = import.meta.env.VITE_QUEUE_URL!;     // must end with /exec
  const token = import.meta.env.VITE_QUEUE_TOKEN!;
  if (!base || !token) {
    throw new Error("PF enqueue not configured (VITE_QUEUE_URL / VITE_QUEUE_TOKEN)");
  }

  // Put token in query string; do NOT send Authorization header.
  const url = `${base}?token=${encodeURIComponent(token)}`;

  // Use no-cors + simple content type to avoid preflight entirely.
  await fetch(url, {
    method: "POST",
    mode: "no-cors",
    headers: { "Content-Type": "text/plain" },
    body: JSON.stringify({ jobs })
  });

  // Response will be "opaque" (cannot read it), but the job is delivered.
  return { ok: true, queued: jobs.length };
}
