import React, { useState } from "react";
import { enqueueJobs } from "../lib/enqueue";

export default function TestEnqueue() {
  const [msg, setMsg] = useState("");
  
  async function handleClick() {
    setMsg("Sending…");
    try {
      const out = await enqueueJobs([
        {
          id: "pf-cloud-test",
          action: "log_opportunity",
          payload: {
            OppID: "TEST-1",
            Title: "Cloud Relay Test",
            Agency: "PF",
            DueDate: "2025-12-31",
            Source: "PF",
            Role: "Sub",
            Scores: { Profitability: 4, StrategicFit: 5 }
          }
        }
      ]);
      setMsg("OK: queued " + (out.queued ?? "1"));
    } catch (e) {
      setMsg("Error: " + e.message);
    }
  }

  return (
    <div style={{
      position: "fixed",
      right: 12,
      bottom: 12,
      padding: 12,
      border: "1px solid #ccc",
      borderRadius: 8,
      background: "#fff",
      zIndex: 1000,
      boxShadow: "0 2px 8px rgba(0,0,0,0.1)"
    }}>
      <div style={{ fontWeight: 600, marginBottom: 6, fontSize: 14 }}>PF Cloud Test</div>
      <button 
        onClick={handleClick} 
        style={{
          padding: "6px 12px",
          cursor: "pointer",
          border: "1px solid #ccc",
          borderRadius: 4,
          background: "#f8f8f8",
          fontSize: 12
        }}
      >
        Enqueue Test Job
      </button>
      <div style={{ marginTop: 6, fontSize: 12, maxWidth: 200, wordBreak: "break-word" }}>{msg}</div>
    </div>
  );
}
